/* footer */
.todo-footer {
    width: 560px;
    height: 40px;
    line-height: 40px;
    padding-left: 6px;
    margin-top: 5px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}
.todo-footer label{
   display: inline-block;
   margin-right: 20px;
   cursor: pointer;

}
.todo-footer label input {
    top: -1px;
    vertical-align: middle;
    margin-right: 2px;
}
.todo-footer button {
    margin-top: 5px;
}
/* Media queries */ 
@media only screen and (max-width: 600px) {
    .todo-footer {
        width: 500px;
    }
}
@media screen and (max-width: 480px) {
    .todo-footer {
        width: 358px;
        height: 80px;
    }
    }
@media screen and (max-width: 320px) {
    .todo-footer {
        width: 250px;
    } 
}

