/*General settings*/
* {
    margin: 0;
    padding: 0;
}
body {
    background: rgb(243, 246, 243);
}
.app-name {
    font-family: Georgia;
    font-size: 28px;
    text-align: center;
    margin: 30px auto;
}
.btn {
    display: inline-block;
    padding: 4px 12px;
    margin-bottom: 0;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2), 0 1px 2px rgba(0, 0, 0, 0.05);
    border-radius: 4px;
}

.btn-danger {
    color: #fff;
    background-color: #da4f49;
    border: 1px solid #bd362f;
}
.btn-danger:hover {
    color: #fff;
    background-color: #bd362f;
}
.btn:focus {
    outline: none;
}
.todo-container {
    margin: 3px auto;
    width: 600px;
}
.todo-container .todo-wrap {
    padding: 6px;
    border: 1px solid #ddd;
    border-radius: 5px;
}
/* Media queries */ 
@media only screen and (max-width: 600px) {
    .todo-container  {
        width: 530px；
    }
}
@media screen and (max-width: 480px) {
    .todo-container {
        width: 370px；
    }
}
@media screen and (max-width: 320px) {
    .todo-container {
        width: 270px;
    }
}